import React from "react";
import "./Jumbotron.css";

const Jumbotron = () => (
	<header className = "header">
		<h1>Tsum Tsum Clicky Game!</h1>
		<h2>Click on any Tsum Tsum to earn a point </h2>
		<h2>Don't click on the same picture more than once.</h2>	 
	</header>
);
export default Jumbotron;


